h1{
    font-size: 2rem;
    text-align: left;
    font-weight: bold;
}
p{
    font-size: 1rem;
    text-align: left;
    padding-top: 1.5vh;
}
.content-wrapper {
    border-radius: 0px;
    width: 45%;
    height: auto;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
    top: -5vw;
}
.page-content{
    padding: 20px;
}
.avatar{
    border-radius: 50%;
    width: 150px;
    height: 150px;
    margin-top: 25px;
}

.navbar{
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    padding-top: 2vh;
    padding-bottom: 2vh;
    z-index: 20;
}

.nav-container{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 45%;
    height: 80px;
    max-width: calc(100% - 100px);
}

.nav-logo{
    align-items: center;
    margin-right: 20px;
    margin-left: 20px;
    cursor: pointer;
    text-decoration: none;
    flex-grow: 1;
    font-size: 2rem;
}

.nav-menu{
    display: flex;
    list-style: none;
    text-align: center;
}

.nav-links{
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    border-bottom: 3px solid transparent;
}

.nav-item{
    line-height: 40px;
    margin-right: 1rem;
    font-size: 1.5rem;
}
.nav-item:after{
    content: "";
    display: block;
    height: 3px;
    width: 0;
    background: transparent;
    transition: width 0.5s ease, background-color 0.5s ease;
}
.nav-icon{
    display:none;
}
.fa-code{
    margin-left: 1rem;
}

#tsparticles {
    z-index: 100;
}

/* Collapse the nav-bar */
@media screen and (max-width: 1520px){
    .nav-menu{
        display: flex;
        flex-direction: column;
        width: 100%;
        border-top: 1pxsolid #fff;
        position: absolute;
        top: 80px;
        left: -110%;
        opacity: 1;
        transition: all 0.4s ease;
    }

    .nav-menu.active{
        left: 0px;
        opacity: 1;
        transition: all 0.4s ease;
        z-index: 1;
    }

    .nav-icon{
        display: block;
        margin-right: 1rem;
    }
}

/* Enlarge the page-content and turn navbar display to block */
@media screen and (max-width: 920px){

    .content-wrapper{
        width: 85%;
    }

    .avatar{
        border-radius: 50%;
        width: 200px;
        height: 200px;
        padding: 0.5vw;
    }

    .nav-container{
        display: block;
        text-align: center;
    }

    .navbar{
        padding-top: 40px;
    }

    .nav-logo{
        margin: 0;
    }

    .nav-icon{
        margin: 0;
        margin-top: 15px;
    }
    .nav-menu.active{
        margin-top: 45px;
        margin-left: 8px;
    }
}
